import './ReviewSlider.scss'

import { Slide } from 'react-slideshow-image'
import 'react-slideshow-image/dist/styles.css'

export const ReviewSlider = () => {
  return (
    <div className="reviews">
      <Slide>
        <div className="slide">
          <ReviewFragment
            nick="mik787"
            quote=" :  -     .  -     .  -  '."
          />
        </div>
        <div className="slide">
          <ReviewFragment
            nick="doctorjuta"
            quote="      minecraft.            -           ."
          />
        </div>
        <div className="slide">
          <ReviewFragment
            nick="kuzia13666"
            quote={
              <div>
                PlayCraft    ,      .
                ,          
                   .    ,   
                     ,   
                    .   ,    
                 ,      IRL. PlayCraft {' '}
                <span className="ico ico-heart">
                  <span className="path1"></span>
                  <span className="path2"></span>
                  <span className="path3"></span>
                  <span className="path4"></span>
                </span>
              </div>
            }
          />
        </div>
        <div className="slide">
          <ReviewFragment
            nick="Master21"
            quote={` PlayCraft   -    .         .     .  8        ,     . ,           ""        . PlayCraft -        .      .`}
          />
        </div>
      </Slide>
    </div>
  )
}

const ReviewFragment = ({ nick, quote }) => {
  return (
    <div className="review">
      <div className="reviewer">
        <img
          className="photo"
          alt="skin head"
          src={`https://login.playcraft.com.ua/action.php?action=head&nick=${nick}&size=200`}
        />
        <div className="name">{nick}</div>
      </div>
      <div className="review-text">
        <div className="quote-start" />
        <div className="quote">{quote}</div>
        <div className="quote-end" />
      </div>
    </div>
  )
}
